<template>
  <div class="casino container">
  	<div class="hint">
  		<h3>Matters needing attention</h3>
  		<p>Ensure that your token performs the function of authorization transfer, otherwise you will not be able to bulk transfer money.</p>
		<p>The number of airdrop accounts should not exceed 50 each time, otherwise it will cause airdrop failure.</p>
  	</div>
    <div class="first" v-if="currentPage == 1">
    	<div class="list">
    		<label >LT Contract Address：</label>
				<Input v-model="tokenAddress" @keyup.enter.native="searchToken" placeholder="Token address" size="large"/>
				<Button type="primary" icon="ios-search" style="margin-top: 20px;" @click="searchToken">Search</Button>
    	</div>
	    <div class="list"><label >Token Name： </label> <p>{{tokenName}}</p></div>
	    <div class="list"><label >Contract TotalSupply： </label> <p>{{tokenTakeNum}}</p></div>
	    <div class="list"><label >Contract Allowance： </label> <p>{{tokenApproveNum}}</p></div>
	    <div class="list"><label >Account Balance：</label> <p>{{tokenHaveNum}}</p></div>
	    
			<Button v-if="tokenAddress != ''" @click="currentPage = 2" size="large" type="primary">
	       	Next
	        <Icon type="ios-arrow-forward" />
	    </Button>
    </div>
    
   <div class="second" v-if="currentPage == 2">
   		  <div class="list"><label >LT Contract Address：</label> <p>{{tokenAddress}}</p></div>
   		  <div class="list">
   		  		Upload File：support
   		  		<a href="./static/images/example.xlsx" title="./static/images/example.xlsx" download style="margin-left: 15px;">xlsx</a>
   		  		<a href="./static/images/리틀비배분수정-NEW.xlsx" title="./static/images/example.xlsx" download style="margin-left: 15px;">xlsx(sky)</a>
   		  </div>
   		  
		    <form name="fileinfo" id="fileinfo">
		    		<div class="upfile">
		    			 <span>Select the file to upload</span>
		    			 <i class="ivu-icon ivu-icon-ios-cloud-upload-outline"></i>
		    			 <input type="file"  title="Select the file to upload" name="file_name"  @change="upFile('fileinfo')" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet">
		    		</div>
		     </form>
		    <form name="fileinfo2" id="fileinfo2">
		    		<div class="upfile" type="info">
		    			 <span>Robot for SKY->Upload EXCEL</span>
		    			 <i class="ivu-icon ivu-icon-ios-cloud-upload-outline"></i>
		    			 <input type="file" title="Robot for SKY->Upload EXCEL" name="file_name"  @change="upFile('fileinfo2')" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet">
		    		</div>
		     		
		     </form>
		     <span v-if="fileIsShow" style="margin-bottom: 20px;display: block;padding: 10px 0 10px 15px;background-color: #fff9e6;">Please upload according to the case format.</span>
		    <div class="list">
		    	<label >Transfer List </label> 
		    	<p style="height: auto;min-height: 72px;">{{tokenNum}}</p>
		    </div>
		    <!--<div class="list">
		    	<label >Transfer Line  </label>
		    	<p style="height: auto;">{{tokenNum}}</p>
		    </div>-->
		    <Button  @click="currentPage = 1" size="large" type="primary">
          <Icon type="ios-arrow-back" />
                 Prev
        </Button>
		    <Button @click="transferFormToken" size="large" type="primary" :disabled="isDisabled">
		       	 Transfer
		        <Icon type="ios-arrow-forward" />
		    </Button>
		    
		    <img v-if="pending" id="loader" src="https://loading.io/spinners/double-ring/lg.double-ring-spinner.gif">
   </div>
    
  </div>
</template>

<script>
export default {
  name: 'casino',
  data () {
    return {
      pending: false,
      
      tokenAddress:'0xfB42AdedFb7D300F214485e0Ac00FD8e76BB6305',
      otherAddress:'',
      otherNum:'',
      tokenNum:'',
      
      tokenName:'',
      tokenTakeNum:'',
      tokenHaveNum:'',
      tokenApproveNum:'',
      
      fileIsShow:false,
      filtInput:'',
      
      isDisabled: false,
      currentPage:1
    }
  },
  methods: {
    searchToken () {
      this.pending = true
      this.$store.state.contractInstance().searchTokenMsg(this.tokenAddress, {
        from: this.$store.state.web3.coinbase
      }, (err, result) => {
        if (err) {
          console.log(err)
          this.pending = false
        } else {
          this.tokenName = result[0];
          this.tokenTakeNum = result[1].c[0];
          this.tokenHaveNum = result[2].c[0];
          this.tokenApproveNum = result[3].c[0];
          this.pending = false
        }
      })
    },
    upFile(obj){
    	let _this = this;
    	let form = new FormData(document.getElementById(obj));
    	let urls = '';
    	
    	if(obj == 'fileinfo'){
    		urls = '/index.php/cn/home/multisender/main_ajax'
    	}else{
    		urls = '/index.php/cn/home/multisender/kr_ajax'
    	}
    	_this.$axios.post(urls, form)
		  .then(function (response) {
				var response = response.data;
				if(response.state == 0){
					_this.otherAddress = '';
					_this.otherNum = '';
					for(let i=0;i<response.info.length;i++){
						if(i>0){
							_this.otherAddress+= ' - ';
							_this.otherNum+= ' - ';
						}
						_this.otherAddress += response.info[i].address;
						_this.otherNum += response.info[i].amount * Math.pow(10,18);
						_this.tokenNum += response.info[i].address + '  =>  ' + response.info[i].amount + '\n';
					}
					_this.fileIsShow = false;
				}else{
					_this.fileIsShow = true;
				}
		  })
		  .catch(function (error) {
		    console.log(error);
				_this.fileIsShow = true;
		  });
		
    },
    transferFormToken () {
    	this.isDisabled = true;
      this.pending = true
      let otherAddressArr = this.otherAddress.split(' - ');
      let otherNumArr = this.otherNum.split(' - ');
      this.$store.state.contractInstance().multisend(this.tokenAddress,otherAddressArr,otherNumArr, {
      	gas: 300000,
        from: this.$store.state.web3.coinbase
      }, (err, result) => {
        if (err) {
          console.log(err)
          this.pending = false
          this.isDisabled = false
        } else {
          this.pending = false
          this.tokenNum = ''
          this.otherAddress = '';
					this.otherNum = '';
          console.log('成功');
          this.isDisabled = false
        }
      })
    }
  },
  mounted () {
    console.log('dispatching getContractInstance')
    this.$store.dispatch('getContractInstance')
  },
  beforeCreate(){
		this.$store.dispatch('registerWeb3');
	}
}
</script>

<style scoped>
.casino {
		width: 700px;
    margin: 30px auto 0;
}
.casino>{
	position: relative;
}
#loader {
  width:150px;
  position: absolute;
  bottom: 0;
}
#has-won {
  color: green;
}
#has-lost {
  color:red;
}

div.hint{
	padding: 16px 24px;
	background: #f8f8f9;
	border-radius: .28571429rem;
	color: #276f86;
	box-shadow: 0 0 0 1px #a9d5de inset, 0 0 0 0 transparent;
	margin-bottom: 20px;
	font-size:  16px;
	line-height: 26px;
}


h1{
	margin-bottom: 20px;
}

div.list{
	text-align: left;
	margin-bottom: 20px;
	font-size: 18px;
}

div.list p{
	height: 36px;
	line-height: 22px;
	background-color: #f3f3f3;
	border: 1px solid #dcdee2;
	border-radius: 4px;
	padding: 6px 7px;
	font-size: 14px;
}

div.list a{
	font-size: 16px;
	margin-right: 20px;
}

label{
	font-size: 20px;
	padding: 0 0 20px;
	display: block;
}

form{
	margin-bottom: 20px;
}

.upfile{
	display: inline-block;
    margin-bottom: 0;
    font-weight: 400;
    text-align: center;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    padding: 5px 15px 6px;
    font-size: 12px;
    border-radius: 4px;
    transition: color .2s linear,background-color .2s linear,border .2s linear,box-shadow .2s linear;
    color: #515a6e;
    background-color: #fff;
    border-color: #dcdee2;
    position: relative;
}
.upfile input{
	position: absolute;
	left: 0;
	right: 0;
	height: 100%;
	opacity: 0;
}

#fileinfo2>div{
	background: #2d8cf0;
	color: white;
	border: 0;
}
</style>




