import Vue from 'vue'
import App from './App'
import axios from 'axios'

import iView from 'iview'
import 'iview/dist/styles/iview.css'

Vue.use(iView);
Vue.prototype.$axios = axios

import {store} from './store/'
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  components: { App },
  template: '<App/>'
})



