<template>
  <header>
  		<div class="container">
  			<div class="nav">
  				<p class="item">LittleBeeX Sender</p>
  			</div>
  			<div class="navbar-end">
			 	 	<p v-if="isInjected" id="has-metamask"><i aria-hidden="true" class="fa fa-check"></i> Metamask logged in</p>
    			<p v-else id="no-metamask"><i aria-hidden="true" class="fa fa-times"></i> Metamask no logged in</p>
  				<p>
  					Contract Address:
  					<a :href="'https://etherscan.io/address/' + coinbase">{{coinbase}}</a>
  				</p>
  				 <p>Node: {{ network }}</p>
  			</div>
  		</div>
  </header>
</template>

<script>
import { NETWORKS } from '../util/constants/network'
import {mapState} from 'vuex'
export default {
  name: 'hello-metamask',
  data(){
  	return{
	  	thisAddressLink:'https://etherscan.io/address/'
  	}
  },
  computed: mapState({
    isInjected: state => state.web3.isInjected,
    network: state => NETWORKS[state.web3.networkId],
    coinbase: state => state.web3.coinbase,
    balance: state => state.web3.balance,
    ethBalance: state => {
      if (state.web3.web3Instance !== null) return state.web3.web3Instance().fromWei(state.web3.balance, 'ether')
    }
  })
}

</script>

<style scoped>
	header{
		margin-bottom: 5px;
    background: #fff;
    box-shadow: 0 1px 2px 0 rgba(34,36,38,.15);
	}
	div.container{
		width: 1200px;
		margin: 0 auto;
		overflow: hidden;
	}
	div.nav{
		float: left;
	}
	div p{
		float: left;
		padding: 15px;
		font-size: 17px;
	}
	div p a{
		font-size: 16px;
	}
	div.navbar-end{
		float: right;
	}

</style>



